body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;box-sizing:border-box;font-size:14px}html{position:relative}.App{width:100%;height:100vh;position:absolute;left:0;top:0}

.header{width:100%;height:40px;background-color:rgba(0,0,0,0.15);display:flex;align-items:center;justify-content:space-around;position:fixed;left:0;top:0}.header .logo{width:80px;height:30px;background-image:url("https://a.trellocdn.com/prgb/dist/images/header-logo-2x.01ef898811a879595cea.png");background-size:cover}.header .theme{width:32px;height:32px;border-radius:50%;border:2px solid #fff;position:absolute;right:30px;cursor:pointer}.header .theme .color-lists{width:96px;height:96px;border-radius:10px;background-color:#fff;position:absolute;padding:5px;top:120%;right:0;display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-gap:5px;gap:5px}.header .theme .color-lists .color-item{width:100%;height:100%;border-radius:50%}

.Board{margin-top:50px;display:flex}.Board .Add-List{width:272px;margin:10px;flex-shrink:0;border-radius:3px}.Board .Add-List .Add-List-Button{background-color:rgba(0,0,0,0.12);border-radius:3px;cursor:pointer;color:#fff;display:flex;align-items:center;min-height:32px;padding:8px 12px;transition:background-color 85ms ease-in, opacity 40ms ease-in, border-color 85ms ease-in;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.Board .Add-List .Add-List-Button:hover{background-color:rgba(0,0,0,0.24)}.Board .Add-List .Add-List-Button .svg-add{margin-right:5px}.Board .Add-List .Add-List-Button .svg-add svg{fill:#fff}

.List{background:#ebecf0;flex-shrink:0;width:272px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:10px;margin-right:0;border-radius:3px;padding:10px}.List .List-Title{cursor:pointer;padding:4px 6px;padding-right:36px;position:relative;overflow-wrap:break-word;font-weight:600}.List .List-Title:hover .delete{opacity:1}.List .List-Title .delete{position:absolute;top:1px;right:5px;opacity:0;width:24px;height:24px;display:flex;justify-content:center;align-items:center;transition:all .3s ease}.List .List-Title .delete:hover{background-color:#f5f6f7}.List .Toggle-Add-Card{cursor:pointer;padding:5px;color:#6b808c;border-radius:3px;display:flex;align-items:center}.List .Toggle-Add-Card:hover{background-color:rgba(9,45,66,0.13);color:#17394d}.Toggle-Add-Card ion-icon{margin-right:2px}

.editButton{display:inline-block;padding:6px 12px;background-color:#5aac44;color:#fff;border-radius:3px;cursor:pointer}.deleteButton{display:inline-block;padding:6px 12px;background-color:lightcoral;color:#fff;border-radius:3px;cursor:pointer;margin-left:5px}

.wrap{background-color:#ebecf0;padding:8px 10px;border-radius:3px}.List-Title-Textarea{font-weight:600;height:28px;width:100%;border-radius:3px;border:none;resize:none;outline:none;font-size:15px;padding:4px 8px}.List-Title-Textarea:focus{box-shadow:inset 0 0 0 2px #0079bf}

.Edit-Card .Card{min-height:50px;padding-left:8px;padding-right:15px}.Edit-Card .Card:hover{background:white}.Edit-Card .Card .Edit-Card-Textarea{width:100%;border:none;resize:none;outline:none}.Edit-Card .edit-btn{display:flex;align-items:center;flex-direction:row}.Edit-Card .edit-btn div{display:flex;justify-content:center;align-items:center;cursor:pointer}.Edit-Card .edit-btn div svg{transition:all .3s ease}.Edit-Card .edit-btn div:hover svg{width:20px;height:20px}

.Card{position:relative;cursor:pointer;background:white;margin:5px 0;padding:6px 8px;border-radius:3px;border:1px solid rgba(0,0,0,0.12);box-shadow:0 1px 0 rgba(9,45,66,0.25);font-size:15px;overflow-wrap:break-word;min-height:18px}.Card:hover{background:#f5f6f7}.Card .Card-Icons{position:absolute;top:3px;right:5px;display:flex;flex-direction:row;justify-content:flex-end}.Card .Card-Icons .Card-Icon{cursor:pointer;width:24px;height:24px;display:flex;align-items:center;justify-content:center;border-radius:5px;margin:1px;color:rgba(0,0,0,0.5);background:#f5f6f7;opacity:0.9}.Card .Card-Icons .Card-Icon:hover{opacity:1;background:#dcdcdc}

.Add-List-Editor{background:#dfe3e6;border-radius:5px;padding:2px 2px}

